import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    products: [],
  },
 
  mutations: {

    addProducts(state, products) {
      state.products = [];
      state.products.push(products);
    },
  },
  actions: {

    fetchProducts({ commit }) {
      fetch('http://localhost:8020/products/products')
        .then( obj => obj.json() )
          .then( res => {
  
            commit('addProducts', res);
          });

    },

  },
  modules: {
  }
})
